.NoteCard {
    background: #403e44;
    padding: 20px;
    margin: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: azure;
  }
  
  .NoteCard p {
    margin: 0 0 10px 0;
  }
  
  .NoteCard button {
    background: #ff0000;
    color: rgb(224, 210, 210);
    border: none;
    padding: 5px 10px;
    cursor: pointer;
  }
  